import { computed, signal } from "@preact/signals"
import { TaxConfig } from "./taxconfig"

export class Stores {
    static about: any = {
        filing_status: signal(0), // 0: single, 1: married separate filing, 2: married file jointly, 3: Head of household
        child_under17_count: signal(0),// number of child dependent under 17
        dependent_count: signal(0), // number of dependent over 17
        has_child: computed(() => {
            return this.about.child_under17_count.value > 0 || this.about.dependent_count.value > 0
        })
    }

    static income: any = {
        w2: {
            enabled: signal(false),
            amt: signal(0),
        },
        rsu: {
            enabled: signal(false),
            amt: signal(0),
        },        
        f401k_match: {
            enabled: signal(false),
            amt: signal(0),
        },
        f401k_contrib: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099b_longterm: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099b_shortterm: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099misc: {
            enabled: signal(false),
            amt: signal(0),
        }, // misc, nec, rental
        f1099r: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099g: {
            enabled: signal(false),
            amt: signal(0),
        },
        farm: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099_div_qualified: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099_div: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099_int_taxfree: {
            enabled: signal(false),
            amt: signal(0),
        },
        f1099_int: {
            enabled: signal(false),
            amt: signal(0),
        },
        gambling_other: {
            enabled: signal(false),
            amt: signal(0),
        },
        espp_discount: {
            enabled: signal(false),
            amt: signal(0),
        },
        ira:  {
            enabled: signal(false),
            amt: signal(0),
        },
        hsa:  {
            enabled: signal(false),
            amt: signal(0),
        },
        fsa:  {
            enabled: signal(false),
            amt: signal(0),
        },
        utma_gain:  {
            enabled: signal(false),
            amt: signal(0),
        },
        income: computed(() => {
            return parseInt(this.income.w2.amt.value) + parseInt(this.income.rsu.amt.value) + parseInt(this.income.f401k_match.amt.value) +
                parseInt(this.income.f1099b_longterm.amt.value) + parseInt(this.income.f1099b_shortterm.amt.value) + parseInt(this.income.f1099_int_taxfree.amt.value) +
                parseInt(this.income.f1099g.amt.value) + parseInt(this.income.f1099misc.amt.value) + parseInt(this.income.f1099r.amt.value) + 
                parseInt(this.income.farm.amt.value) + parseInt(this.income.f1099_int.amt.value) + parseInt(this.income.f1099_div.amt.value) + 
                parseInt(this.income.f1099_div_qualified.amt.value) + parseInt(this.income.gambling_other.amt.value) + parseInt(this.income.espp_discount.amt.value) +
                parseInt(this.income.utma_gain.amt.value)
        }),              
        pretax: computed(() => {
            return parseInt(this.income.fsa.amt.value) + parseInt(this.income.hsa.amt.value) +
                parseInt(this.income.ira.amt.value) +  parseInt(this.income.f1099_int_taxfree.amt.value) +
                parseInt(this.income.f401k_match.amt.value) + parseInt(this.income.f401k_contrib.amt.value) + 
                parseInt(this.income.utma_gain.amt.value)
        }),
        magi: computed(() => {
            return this.income.agi + parseInt(this.income.f1099_int_taxfree.amt.value)
        }),
        agi: computed(() => {
                return this.income.income - this.income.pretax
        }),
        tax_advantage_income: computed(() => {
                return this.income.pretax + parseInt(this.income.espp_discount.amt.value) +
                    parseInt(this.income.f1099_div_qualified.amt.value) + parseInt(this.income.f1099b_longterm.amt.value)
        })
    }

    static deduction: any = {
        medical_dental_expense:  {
            enabled: signal(false),
            amt: signal(0),
        },
        standard_deduction:  {
            enabled: signal(false),
            amt: signal(0),
        },
        business_deduction:  {
            enabled: signal(false),
            amt: signal(0),
        },
        mortgage_interest:  {
            enabled: signal(false),
            amt: signal(0),
        },
        salt:  {
            enabled: signal(false),
            amt: signal(0),
        },
        charity_contribution:  {
            enabled: signal(false),
            amt: signal(0),
        },
        other_deduction:  {
            enabled: signal(false),
            amt: signal(0),
        },
    
        college_expense:  {
            enabled: signal(false),
            amt: signal(0),
        },
        early_withdrawal_penalty:  {
            enabled: signal(false),
            amt: signal(0),
        },
        investment_expense:  {
            enabled: signal(false),
            amt: signal(0),
        },
        f529_contribution:  {
            enabled: signal(false),
            amt: signal(0),
        },
        utma_contribution:  {
            enabled: signal(false),
            amt: signal(0),
        },  
        roth_ira:  {
            enabled: signal(false),
            amt: signal(0),
        },            

        itemized_deduction: computed(() => {
            let medical_dental_qualification = 0.075 * Stores.income.agi.value
            let medical_dental_allowed = Math.max(0, parseInt(this.deduction.medical_dental_expense.amt.value) - medical_dental_qualification)
            
            return parseInt(this.deduction.business_deduction.amt.value) + medical_dental_allowed + 
                parseInt(this.deduction.mortgage_interest.amt.value) +  parseInt(this.deduction.salt.amt.value) +
                parseInt(this.deduction.charity_contribution.amt.value) + parseInt(this.deduction.college_expense.amt.value) + 
                parseInt(this.deduction.early_withdrawal_penalty.amt.value) + parseInt(this.deduction.investment_expense.amt.value) +
                parseInt(this.deduction.other_deduction.amt.value)
        }),

        standardized_deduction: computed(() => {
            let std = 0
            let filing = this.about.filing_status.value
            // 0: single, 1: married separate filing, 2: married file jointly, 3: Head of household
            switch(filing) {
                case 0:
                case 1: std = 12950; break;
                case 2: std = 25900; break;
                case 3: std = 19400; break;
                default: std = 0; break;
            }

            return std
        }),
        deduction: computed(() => {
            let res = []
            let std = parseInt(this.deduction.standardized_deduction.value)
            let itemize = parseInt(this.deduction.itemized_deduction.value)

            if(std > itemize) {
                res = ['Standard Deduction',std]
            } else {
                res = ['Itemized Deduction', itemize]
            }
            return res
        })

    }

    static taxcredit: any = {
        eic:  {
            enabled: signal(false),
            amt: signal(0),
        },  
        child_tax_credit: {
            enabled: signal(false),
            amt: computed(() => {
                let enable = false
                if(this.about.filing_status.value === 2) {
                    enable = (this.income.magi < 400000)
                } else {
                    enable = (this.income.magi < 200000)
                }

                if(enable) {
                    if(this.about.child_under17_count.value === 0) {
                        enable = false
                    }
                }

                let res = 0
                if(enable) {
                    res = (this.about.child_under17_count.value * 2000)
                }
                return res
            })
        },  
        dependent_credit:  {
            enabled: signal(false),
            amt: computed(() => {
                let enable = false
                if(this.about.filing_status.value == 2) {
                    enable = (this.income.magi < 400000)
                } else {
                    enable = (this.income.magi < 200000)
                }

                if(enable) {
                    if(this.about.dependent_count.value === 0) {
                        enable = false
                    }
                }

                let res = 0
                if(enable) {
                    res = (this.about.dependent_count.value * 500)
                }
                return res
            })
        },  
        child_dependent_care_expense: {
            enabled: signal(false),
            amt: signal(0),
        },  
        premium_tax_credit:  {
            enabled: signal(false),
            amt: signal(0),
        },  
        residential_energy_credit:  {
            enabled: signal(false),
            amt: signal(0),
        },  
        ev_credit:  {
            enabled: signal(false),
            amt: signal(0),
        },  
        foreign_tax_credit:  {
            enabled: signal(false),
            amt: signal(0),
        },
        total_tax_credit: computed(() => {
            return parseInt(this.taxcredit.eic.amt.value) + parseInt(this.taxcredit.child_tax_credit.amt.value) + 
                    parseInt(this.taxcredit.dependent_credit.amt.value) + parseInt(this.taxcredit.child_dependent_care_expense.amt.value) + 
                    parseInt(this.taxcredit.premium_tax_credit.amt.value) + parseInt(this.taxcredit.residential_energy_credit.amt.value) + 
                    parseInt(this.taxcredit.ev_credit.amt.value) + parseInt(this.taxcredit.foreign_tax_credit.amt.value)

        })
    }

    static tax : any = {
        taxable_income: computed(() => {
            return (Math.max(this.income.agi.value - this.deduction.deduction.value[1], 0)).toFixed(0)
        }),
        tax_amount: computed(() => {
            let longterm_capgain = parseInt(this.income.f1099b_longterm.amt.value) + parseInt(this.income.f1099_div_qualified.amt.value)
            return (TaxConfig.getTaxAmount(this.tax.taxable_income.value, longterm_capgain, Stores.about.filing_status.value)).toFixed(0)
        }),
        effective_tax_rate: computed(() => {
            return ((this.tax.tax_amount.value / this.tax.taxable_income.value) * 100).toFixed(2)
        }),
        due: computed(() => {
            return (this.tax.tax_amount.value - this.taxcredit.total_tax_credit.value).toFixed(0)
        })
    }
    
}
