import { h } from 'preact';

const ReadonlyIncomeInput = (props: any) => {

    return (
        <div class="mb-3 border rounded" style="width:450px;padding:5px;">
            <div class="input-group mb-3">
                <span class="input-group-text" style="width:180px;font-size:10pt;">{props.title}</span>
                <div class="input-group-text">&nbsp;&nbsp;
                </div>
                <span class="input-group-text">$</span>
                <input type="number" id="txt_amt" value={props.store.amt.value} aria-label="Amount" class="form-control text-end" disabled={true}></input>
                <span class="input-group-text">.00</span>
            </div>
            <label style="font-size:11px;padding-left:4px;">{props.description}</label>
        </div>
    )
}

export default ReadonlyIncomeInput;